import Link from "next/link";
import { Badge } from "@/components/ui/badge";
import { Separator } from "@/components/ui/separator";
import { Button } from "@/components/ui/button";
import cases from "@/data/mock_cases.json";
import { CaseSchema } from "@/lib/schema";

export default async function CaseDetailPage({
  params,
}: {
  params: Promise<{ id: string }>;
}) {
  const { id } = await params;

  const found = (cases as any[]).find((c) => c.id === id);
  if (!found) throw new Error("Case not found");

  const c = CaseSchema.parse(found);

  return (
    <div className="p-6 space-y-4">
      <div className="flex items-center gap-4">
        <Link className="underline" href="/cases">
          Back
        </Link>
        <h1 className="text-2xl font-semibold">{c.id}</h1>
        <span className="text-sm text-muted-foreground">{c.status}</span>
      </div>

      <div className="border rounded-lg p-4 space-y-2">
        <div className="font-semibold">Summary</div>
        <div>User: {c.user_display}</div>
        <div>User message: {c.user_message}</div>
        <div>Tool: {c.tool_name}</div>
        <div className="flex items-center gap-2">
          <span>Risk:</span>
          <Badge>{c.risk_label}</Badge>
          <span>({c.risk_score})</span>
        </div>
      </div>

      <Separator />

      <div className="border rounded-lg p-4 space-y-2">
        <div className="font-semibold">Tool args (redacted)</div>
        <pre className="text-xs bg-muted p-3 rounded">
          {JSON.stringify(c.tool_args_redacted, null, 2)}
        </pre>
      </div>

      <div className="border rounded-lg p-4 space-y-2">
        <div className="font-semibold">Rationale</div>
        <div>{c.risk_rationale}</div>
        <div className="text-sm text-muted-foreground">
          Policy refs: {c.policy_refs.join(", ")}
        </div>
      </div>

      <div className="border rounded-lg p-4 space-y-2">
        <div className="font-semibold">Decision</div>
        <div className="flex gap-2">
          <Button>Approve</Button>
          <Button variant="secondary">Reject</Button>
          <Button variant="secondary">Request info</Button>
        </div>
        <div className="text-sm text-muted-foreground">
          MVP: wire these to a POST endpoint later.
        </div>
      </div>

      <div className="border rounded-lg p-4 space-y-2">
        <div className="font-semibold">Audit trail</div>
        <ul className="text-sm space-y-1">
          {c.history.map((h, idx) => (
            <li key={idx}>
              {h.ts} - {h.actor} - {h.event}: {h.detail}
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
}
